
import { languages } from '@/app/oferta-academica/LanguageData'; // Using a separate data file
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { ArrowLeft, Download, FileText } from 'lucide-react';
import Link from 'next/link';
import Image from 'next/image';

// This function would typically fetch data based on slug
// For now, we'll find it from the imported array
async function getLanguageData(slug: string) {
  return languages.find(lang => lang.slug === slug);
}

export async function generateStaticParams() {
  return languages.map((lang) => ({
    languageName: lang.slug,
  }));
}

export default async function LanguageDetailPage({ params }: { params: { languageName: string } }) {
  const language = await getLanguageData(params.languageName);

  if (!language) {
    return (
      <div className="container mx-auto px-4 md:px-6 py-16 text-center">
        <h1 className="text-3xl font-bold text-destructive mb-4">Idioma no encontrado</h1>
        <p className="text-lg text-muted-foreground mb-6">El idioma que buscas no existe o ha sido movido.</p>
        <Button asChild>
          <Link href="/oferta-academica"><ArrowLeft className="mr-2 h-4 w-4" />Volver a Oferta Académica</Link>
        </Button>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 md:px-6 py-12 md:py-16 bg-muted/30 min-h-[calc(100vh-10rem)]">
      <header className="mb-8">
        <Button variant="outline" asChild className="mb-6">
          <Link href="/oferta-academica"><ArrowLeft className="mr-2 h-4 w-4" />Volver a Oferta Académica</Link>
        </Button>
        <div className="flex flex-col md:flex-row items-center gap-6 mb-6">
            {language.image && (
                 <Image 
                    src={language.image} 
                    alt={`Curso de ${language.name}`} 
                    width={150} 
                    height={100} 
                    className="rounded-lg shadow-md object-cover md:w-48 md:h-32"
                    data-ai-hint={language.aiHint || 'language course icon'}
                />
            )}
            <div>
                <h1 className="font-headline text-4xl md:text-5xl font-bold text-primary">{language.name}</h1>
                <p className="text-lg text-foreground/80 mt-1">{language.description}</p>
            </div>
        </div>
      </header>

      <Card className="shadow-xl border-primary/30 border bg-card overflow-hidden">
        <CardHeader className="bg-primary/5">
          <CardTitle className="text-2xl text-primary flex items-center">
            <FileText className="mr-3 h-7 w-7" />
            Brochure Informativo: {language.name}
          </CardTitle>
          <CardDescription>
            Consulta todos los detalles del programa, niveles, horarios y más en nuestro brochure oficial.
          </CardDescription>
        </CardHeader>
        <CardContent className="p-0 md:p-4">
          {language.pdfUrl ? (
            <>
              <div className="aspect-[4/3] md:aspect-video w-full bg-gray-200 rounded-b-lg md:rounded-lg overflow-hidden">
                <iframe
                  src={`${language.pdfUrl}#toolbar=0&navpanes=0&scrollbar=1`}
                  width="100%"
                  height="100%"
                  style={{ border: 'none' }}
                  title={`Brochure del curso de ${language.name}`}
                  className="min-h-[500px] md:min-h-[700px]"
                >
                  <p className="p-4 text-center">
                    Tu navegador no soporta PDFs incrustados. Puedes descargarlo 
                    <a href={language.pdfUrl} download className="text-primary hover:underline"> aquí</a>.
                  </p>
                </iframe>
              </div>
              <div className="p-4 text-center md:text-right">
                <Button asChild variant="outline">
                  <a href={language.pdfUrl} target="_blank" rel="noopener noreferrer" download={`Brochure-${language.name}.pdf`}>
                    <Download className="mr-2 h-4 w-4" />
                    Descargar Brochure (PDF)
                  </a>
                </Button>
              </div>
            </>
          ) : (
            <div className="p-8 text-center text-muted-foreground">
              <p>El brochure para este idioma no está disponible en este momento. Por favor, inténtalo más tarde o contacta con nosotros.</p>
            </div>
          )}
        </CardContent>
      </Card>

      <div className="mt-12 text-center">
        <h3 className="font-headline text-2xl text-primary mb-4">¿Interesado en {language.name}?</h3>
        <p className="text-lg text-foreground/80 mb-6">
          Si tienes más preguntas o deseas saber sobre las próximas inscripciones, no dudes en contactarnos.
        </p>
        <div className="flex flex-col sm:flex-row justify-center gap-4">
          <Button asChild size="lg">
            <Link href="/inscripciones">Ver Proceso de Inscripción</Link>
          </Button>
          <Button asChild variant="outline" size="lg">
            <Link href="/contacto">Contactar Ahora</Link>
          </Button>
        </div>
      </div>
    </div>
  );
}

